<template>
<div class="user mescroll">
  <div class="com">
    <van-panel >
        <div class="title">我的直推</div>  
        <van-cell class="item" id="fil_time_1" >
            <p>{{this.zhituirenshu? this.zhituirenshu : '0'}}</p> 
            <span style="color:gold">人数</span>
        </van-cell>
        <van-cell class="item" id="fil_time_2" >
            <p>{{zhituiyeji?zhituiyeji : '0'}}</p>
            <span style="color:gold">业绩</span>
        </van-cell>
    </van-panel>
    <van-panel>
        <div class="title">我的团队</div> 
        <van-cell class="item" id="fil_time_1" >
            <p>{{tuanduirenshu?tuanduirenshu : '0'}}</p> 
            <span style="color:gold">人数</span>
        </van-cell>
        <van-cell class="item" id="fil_time_2" >
            <p>{{tuanduiyeji?tuanduiyeji : '0'}}</p>
            <span style="color:gold"> 业绩</span>
        </van-cell>

     </van-panel> 
  </div>
  <div class="com1" > <!--v-if="userLevel > 0"-->
      <van-panel >
        <p class="title">我的推广</p> 
        <van-cell class="item" title="我的团队" is-link to="myTeam" />
        <van-cell class="item" title="我的邀请码" is-link to="recommend" />
      </van-panel>
  </div>
</div>
</template>
<script> 
import { Cell, CellGroup } from "vant";
import { getFrame,getPersonalInfo  } from "@/api";
import MeScroll from "mescroll.js";
export default {
    components: {
     [Cell.name]:Cell,
     [CellGroup .name]:CellGroup,
     
  },
  // components: {
  //   [SwipeCell.name]: SwipeCell,
  //   [SwitchCell.name]: SwitchCell,
  //   [Dialog.name]: Dialog
  // },
  data() {
    return {
      zhituirenshu: '0',
      zhituiyeji: '0',
      tuanduirenshu: '0',
      tuanduiyeji: '0',
      userLevel: 0,
      realName: "",
      headPic: "",
      userCode: "",
      empType: 0,
    };
  },
  methods: {
       loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
     getFrame().then(res => {
        if (res.code == 0) {
          this.zhituirenshu = res.data.zhituiren;
          this.zhituiyeji = res.data.zhituiyeji;
          this.tuanduirenshu = res.data.tuanduiren;
          this.tuanduiyeji = res.data.tuanduiyeji
          this.$toast.success(res.msg);
          
        } else {
          this.$toast.fail(res.msg);
        }
        console.log(res.data)
      });
       getPersonalInfo().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.realName = res.data.userName;
          this.headPic = res.data.userHeader;
          this.empType = parseInt(res.data.empType);
          this.userLevel = parseInt(res.data.userLevel);
          sessionStorage.setItem("userCode", res.data.userCode);
        } else {
          this.$toast.fail(res.msg);
        }
      });
    }, 

  },
    mounted() {
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
 .com {
  .title {
    
    height: 35px;
    line-height: 35px;
    margin: 0 auto;
    font-size: 16px;
    font-weight: 400;
    padding-left: 10px;
    border-bottom: 1px solid #2e3b43; 
    background: #2e3b43 ;
    color: goldenrod;
    text-align: center;
  } 
  .van-cell__value{
    text-align: center;
  }
  /deep/.van-cell::after{
    border-bottom:1px solid #253238;
  }
  /deep/.van-panel__header{
     background: #253238;
  }
  /deep/.van-hairline--top-bottom::after{
    border-width:0
  }
  .van-panel{
    margin-bottom: 10px;
  }
  .van-panel__content{
    background: #253238;
  }
  .item{
    display: inline-block; 
    width: 49%;
    height: 90px;  
    text-align: center;  
    background: #253238;
    p{
      font-size: 24px;
      color: goldenrod;
      font-weight: 600;
      line-height: 50px;
      text-align: center;  
    }
    span{ 
      font-size: 12px; 
    }
  } 
   
}
 .com1 {   
  .title {
    height: 35px;
    line-height: 35px;
    margin: 0 auto;
    font-size: 16px;
    font-weight: 400;
    padding-left: 10px;
    border-bottom: 1px solid #2e3b43; 
    background: #2e3b43 ;
    color: goldenrod;
    text-align: center;
  }
  .van-cell__value{
    text-align: center;
  }
  /deep/.van-cell::after{
    border-bottom:1px solid #253238;
  }
  /deep/.van-panel__header{
     background: #253238;
  }
  /deep/.van-hairline--top-bottom::after{
    border-width:0
  }
  .van-panel{
    margin-bottom: 10px;
  }
  .van-panel__content{
    background: #253238;
  }
  .item{
    display: flex;
    width: 96vw;
    height: 45px; 
    margin: 0 auto;
    background: #253238; 
    .title{ 
      font-size: 24px;
      color: goldenrod;
      text-align: center;  
      .tu{
        margin-top: 14px;
        margin-left: 18rem;
      }
    }
    span{ 
      font-size: 24px;
      color: goldenrod; 
      font-size: 14px; 
    } 
  }
}

</style>

